import React from "react"
import {AppContext}  from "../contextCreateBoot"
import { MDBTable, MDBTableBody, MDBTableHead } from 'mdbreact';
import { MDBBtn } from "mdbreact"
import uuidv4 from "../utility"
import TestingResult from "./TestingResult"


class ManualTest extends React.Component {
    static contextType = AppContext
    constructor(props) {
        super(props)
    }

    render () {
        return this.context.testingResults.map((testingResult,id)=>{
            let isError = false
            testingResult.results.map(result=>{
                console.log("result.logDebugsSearchedResult.error: ", result.logDebugsSearchedResult.error)
            if (result.logDebugsSearchedResult.error.length > 0) {
                    isError = true
                    console.log("this test", testingResult, "error")
                }
            })

            return (
                <div key={uuidv4()}>
                     <MDBBtn
                        outline
                        color={(()=>{
                            if(isError) {
                                return "danger"
                            } else {
                                return "mdb-color"
                            }
                        })()}
                        onClick={this.context.toogleShowTestManual.bind(null, id)}
                     >
                        {(()=>{
                            if(isError) {
                                return (<div>TestName: {testingResult.testingName}<strong> Error </strong>Vui Lng B Bi Test  Test Li</div>)
                            } else {
                                return (<div>TestName: {testingResult.testingName}<strong> Pass </strong>Click Vo y  Test Manual</div>)
                            }
                        })()}
                     </MDBBtn>
                     <MDBBtn
                        outline
                        color="warning"
                     >B Bi Test V Test Li</MDBBtn>
                    {(()=>{
                        if (testingResult.showTestManual) {
                            return (<div>
                                <p><strong> Bn Hy M Mt Np Bia Gi V Tr Li Nhng Cu Hi Di y</strong></p>
                                <strong>LED c Sng  Ba Mu Khng C Sng Khng ?: </strong><input type="checkbox" id="myCheck"></input>
                            </div>)
                        }
                    })()}
            </div>)
        })
    }
}

export default ManualTest;