import React, { Component } from "react";
import { BrowserRouter as Router, Route, Link } from "react-router-dom";

import io from "socket.io-client";

import MainApp from "./views/MainApp";
import SerialDisplay from "./views/SerialDisplay";
import Qctool from "./views/Components/Qctool"

import AutomaticTest from './views/AutomaticTest'

import QctoolFactory from "./views/QctoolFactory"

import TestMQTTProtocol from './views/TestMQTTProtocol'

// import Dashboard from "./views/examples/Dashboard"

import {SocketIoContext} from "./views/contextCreateBoot"
import uuidv4 from "./views/utility"
// import CreateBoot from "views/CreateBoot";
// import SimpleModalWrapped from "views/CreateBoot";

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      serialData: '',
      bootSocket: null
    };
    const BOOT_SOCKET_URL = "bootSocket";
    this.bootSocket = io.connect(`/${BOOT_SOCKET_URL}`)
    this.backupSerialData = ''
  }

  storeSerialData = data => {
    let d = data  
    this.setState(state=>{
      let data = state.serialData + d;
      // console.log(data)
      return {
        serialData: data
      }
    });
  };

  setBackupSerialData = (data) => {
    this.setBackupSerialData = data
  }

  // shouldComponentUpdate(nexProp, nextState) {
  //   return this.state.serialData === nextState.serialData 
  // }

  componentDidMount = () => {
    document.title = "QC-TOOL"
  }

  componentWillMount = () => {
    // this.connectSocket();
  }

  connectSocket = () => {
    const BOOT_SOCKET_URL = "bootSocket";
    let bootSocket = io.connect(`/${BOOT_SOCKET_URL}`);

    bootSocket.on("connect", ms => {
      console.log("serial component connected to socket");
    });

    // bootSocket.on("serialData", this.storeSerialData);
    
    // this.bootSocket = bootSocket
    this.setState(state=>{
      return {
        bootSocket: bootSocket
      }
    })
  
  };

  render() {
    return (
      <Router>
        <SocketIoContext.Provider value={
          { bootSocket: this.bootSocket,
            serialData: this.state.serialData,
            backupSerialData: this.backupSerialData,
            setBackupSerialData: this.setBackupSerialData
          }
        }>
        <>
        <Route path="/" exact component={MainApp}></Route>
        <Route path="/manual-test" component={QctoolFactory}></Route>
        <Route path="/automatic-test" component={AutomaticTest}></Route>
        <Route path="/test-mqtt" component={TestMQTTProtocol} />
        {/* <Route path="/example" component={Dashboard}></Route> */}
        </>
        </SocketIoContext.Provider>
      </Router>
    );
  }
}

export default App;
