import React from "react";

import "@fortawesome/fontawesome-free/css/all.min.css";
import "bootstrap-css-only/css/bootstrap.min.css";
import "mdbreact/dist/css/mdb.css";
import {
    MDBBtn
  } from "mdbreact";

class SwitchButton extends React.Component {

    renderNameButton = state => {
      if (state) {
        return <i className="fas fa-toggle-on">ON</i>;
      } else {
        return <i className="fas fa-toggle-off">OFF</i>;
      }
    };
  
    render() {
      return (
        <MDBBtn
          color="mdb-color"
          outline={!this.props.pinState}
          onClick={() => {
            if (this.props.onClick != null) {
              // send pinSetting State to parent
              this.props.onClick();
            }
          }}
        >
          {this.renderNameButton(this.props.pinState)}
        </MDBBtn>
      );
    }
  }

  export default SwitchButton