import React from "react";
import {
  MDBDropdown,
  MDBDropdownToggle,
  MDBDropdownMenu,
  MDBDropdownItem,
  MDBBtn,
  MDBRow,
  MDBCol,
  MDBBtnGroup
} from "mdbreact";
import { AppContext,  ScenarioTestFactoryContext} from "../contextCreateBoot";
import axios from "axios";
import uuidv4 from "../utility";


class BootChoosing extends React.Component {
  static contextType = AppContext;
  constructor(props) {
    super(props);

    this.state = {
      bootInfos: []
    };
  }

  componentWillMount() {
    this.getBootInfo();
  }

  componentDidUpdate() {

  }

  getBootInfo = () => {
    axios.get("/api/get_boot_infos").then(data => {
      let dataBoots = data.data.data;
      let bootInfos = [];
      dataBoots.map(d => {
        bootInfos.push({
          bootName: d.bootName,
          description: d.description,
          data: d.data,
          id: d._id,
          key: uuidv4(),
          onMouseEnter: false
        });
      });

      this.setState(state => {
        return {
          bootInfos: bootInfos
        };
      });

      console.log("get boot infos ", dataBoots);
    });
  };

  mouseEnterChoosingItemHandle = (item, mouseEnter) => {
    let idItem = this.state.bootInfos.indexOf(item);
    // console.log("item ", item, "idItem", idItem)
    this.setState(state=>{
      // console.log("state.bootInfos[idItem]", state.bootInfos[idItem])
      state.bootInfos[idItem].onMouseEnter = mouseEnter;
      return {
        bootInfos: state.bootInfos
      }
    })
  }

  renderItems = () => {
    return this.state.bootInfos.map(info => {
      return (
        <div key={info.key} style={{width:"100%"}}
        onMouseEnter={this.mouseEnterChoosingItemHandle.bind(null, info, true)}
        onMouseLeave={this.mouseEnterChoosingItemHandle.bind(null, info, false)}>
          {(()=>{
            if (info.onMouseEnter) {
              return ( <MDBBtnGroup>
                <MDBBtn
                  outline
                  color="mdb-color"
                  onClick={() => {
                    let d = JSON.parse(info.data)
                    let scenarioTests = d
                    let name = info.bootName
                    this.context.choosedBootHandle(name, scenarioTests)
                  }}                 
                >
                  Choose Boot
                </MDBBtn>
                <MDBBtn
                  outline
                  color="warning"
                  onClick={() => {
                    axios
                      .get(`/api/deleteBoot?id=${info.id}`)
                      .then(res => {
                      });
                  }}
                >
                  Delete Boot
                </MDBBtn>
              </MDBBtnGroup> )
              } else {
              return (<MDBDropdownItem> <b>Name: </b> {info.bootName} <b>Description: </b>{info.description}</MDBDropdownItem>)
              }
            }
          )()
        } 
      </div>)
    })
  }

  render() {
    return (
      <MDBDropdown>
        <MDBDropdownToggle
          color="mdb-color"
          onClick={() => {
            this.getBootInfo();
          }}
          disabled={this.props.disabled}
          outline
        >
          Choose Boot
        </MDBDropdownToggle>
        <MDBDropdownMenu basic>
            {this.renderItems()}
            <MDBBtn 
              style={{width:"100%"}}
              color="mdb-color"
              outline
              onClick={()=>{
                this.context.setStoreScenarioTestsToGlobal([])
                this.context.setCreateBootView(true)
              }}
            >Create New Boot</MDBBtn>
        </MDBDropdownMenu>
      </MDBDropdown>
    );
  }
}

export default BootChoosing;
