var uuidv4Obj = {
  hasBeenAllocated: [],
  
  _uuidv4: function() {
      return "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(/[xy]/g, function(c) {
          var r = (Math.random() * 16) | 0,
          v = c === "x" ? r : (r & 0x3) | 0x8;
        return v.toString(16);
      });
  },

  create: function () {
      while (true) {
          var uuid = this._uuidv4()

          if (this.hasBeenAllocated.indexOf(uuid) < 0) {
              this.hasBeenAllocated.push(uuid)
              // console.log("this.hasBeenAllocated: ", this.hasBeenAllocated)
              break
          }
      }
      
      return uuid
  }
}

var uuidv4 = function() {
    return uuidv4Obj.create.call(uuidv4Obj)
}
export default uuidv4