import React, { Component } from "react";
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import FormGroup from '@material-ui/core/FormGroup';
import TextField from '@material-ui/core/TextField';
import {SocketIoContext}  from "./contextCreateBoot"
import './serialDisplay.css'
import { FormLabel } from "@material-ui/core";

class SerialDisplay extends Component {
  static contextType = SocketIoContext
  constructor(props) {
    super(props)

    this.state = {
      serialData: [],
      serialCommandInput: '',
      senddingSerialCommandFail: null,
      isAutoScroll: true
    }
    this.textLog = React.createRef();
  }

  renderSerialCommandInput = () => (
      <TextField
        style={{
          marginBottom: '22px'
        }} 
        id="standard-basic" 
        onChange={(e)=>{
          var value = e.target.value
          this.setState(state=>{
              return {
                  serialCommandInput: value
              }
          })
        }}
        onKeyDown={(e)=>{
            console.log("send command")
            if(e.key==="Enter") {
                this.context.bootSocket.emit("sendSerial", this.state.serialCommandInput)
            }
        }}
        fullWidth={true}
        label="Box Command" 
    />
)
  
  shouldComponentUpdate(nextProp, nextState) {
    return nextState.serialData !== this.state.serialData 
  }

  autoScrollingCheckBox = (
    <FormControlLabel
      control={<Checkbox
        onChange={(e) => {
          let checked = e.target.checked
          this.setState(state => ({
            isAutoScroll: !checked
          }))
        }}
      />}
      label="Stop Auto Scroll"
    />
  )

  renderSerialData() {
    return this.state.serialData.map(data=>{
      return (
      <p>{data}</p>
      )
    })
  }

  componentDidMount() {
    this.context.bootSocket.on("serialData", (serialData)=>{
      let d = [...this.state.serialData]
      d.push(serialData)
      if (d.length> 2000) d = []
      this.setState(state=>{
        return {
          serialData: d
        }
      })
    })
  }

  componentWillUnmount() {
    this.context.bootSocket.removeListener("serialData")
    // this.context.setBackupSerialData(this.state.serialData)
    console.log("serial display unmount")
  }


  componentDidUpdate() {
    if(this.state.isAutoScroll) this.textLog.current.scrollTop = this.textLog.current.scrollHeight; 
  }

  render() {
    return (
        <div>
          <div style={{
            display: 'flex'
          }}>
            {this.autoScrollingCheckBox} 
            {this.renderSerialCommandInput()}
          </div>
          <div ref={this.textLog} class="serialDisplay">
            {this.renderSerialData()}
          </div>
        </div>
    );
  }
}

export default SerialDisplay;

