import React from "react";

import "@fortawesome/fontawesome-free/css/all.min.css";
import "bootstrap-css-only/css/bootstrap.min.css";
import "mdbreact/dist/css/mdb.css";

import { MDBBtn, MDBRow, MDBCol, MDBBtnGroup, MDBInput } from "mdbreact";

import ScenarioTest from "./Components/ScenarioTest";
import { ScenarioTestFactoryContext, AppContext } from "./contextCreateBoot";

import uuidv4 from "./utility";
import axios from 'axios';

class ScenarioTestFactory extends React.Component {
  static contextType = AppContext;

  constructor(props) {
    super(props);
    this.state = {
      saveButton: {
        openForm: false,
        inputNameBoot: "",
        inputDescription: ""
      }
    };
  }

  appendNewScenarioTest = (name, numLoop) => {
    const scenarioTestsGlobal = [...this.context.scenarioTestsGlobal]
    scenarioTestsGlobal.push({
      key: uuidv4(),
      actions: []
    })
    this.context.setStoreScenarioTestsToGlobal(scenarioTestsGlobal);
  };

  deleteScenarioTest = scenarioTestId => {
    const scenarioTestsGlobal = [...this.context.scenarioTestsGlobal]
    scenarioTestsGlobal.splice(scenarioTestId, 1);
    this.context.setStoreScenarioTestsToGlobal(scenarioTestsGlobal);
  };

  scenarioTestStoreName = (scenarioTestId, name) => {
    const scenarioTestsGlobal = [...this.context.scenarioTestsGlobal]
    scenarioTestsGlobal[scenarioTestId].name = name;
    this.context.setStoreScenarioTestsToGlobal(scenarioTestsGlobal);
  };

  scenarioTestStoreNumloop = (scenarioTestId, numLoop) => {
    const scenarioTestsGlobal = [...this.context.scenarioTestsGlobal]
    scenarioTestsGlobal[scenarioTestId].numLoop = numLoop;
    this.context.setStoreScenarioTestsToGlobal(scenarioTestsGlobal);
  };

  deleteAction = (scenarioTestId, idAction) => {
    const scenarioTestsGlobal = [...this.context.scenarioTestsGlobal]
    scenarioTestsGlobal[scenarioTestId].actions.splice(idAction, 1);

    console.log("scenarioTestsGlobal[scenarioTestId].actions: ", scenarioTestsGlobal[scenarioTestId].actions)
    this.context.setStoreScenarioTestsToGlobal(scenarioTestsGlobal);
  };

  scenarioTestSynchronousState = (scenarioTestId, scenarioTestStore) => {
    let scenarioTestsGlobal = [...this.context.scenarioTestsGlobal]
    scenarioTestsGlobal[scenarioTestId] = scenarioTestStore

    this.context.setStoreScenarioTestsToGlobal(scenarioTestsGlobal);
  };

  actionSynchronousState = (idScenarioTest, idAction, actionStore) => {
    let scenarioTestsGlobal = [...this.context.scenarioTestsGlobal]
    scenarioTestsGlobal[idScenarioTest].actions[idAction] = actionStore
    this.context.setStoreScenarioTestsToGlobal(scenarioTestsGlobal);
  };

  renderSaveButton = () => {
    if (this.state.saveButton.openForm) {
      return (
        <MDBRow>
          {/* <MDBCol>
            <input
              onChange={e => {
                var valueInput = e.target.value
                this.setState(state => {
                  state.saveButton.inputNameBoot = valueInput;
                  return {
                    saveButton: state.saveButton
                  };
                });
              }}
              label="Name Boot"
            />
            <input
              onChange={e => {
                var valueInput = e.target.value
                this.setState(state => {
                  state.saveButton.inputDescription = valueInput;
                  return {
                    saveButton: state.saveButton
                  };
                });
              }}
              label="Description"
            />
          </MDBCol> */}



          <input
            placeholder="Boot name"
            style={{marginRight: 20, marginLeft: 20}}
            onChange={e => {
              var valueInput = e.target.value
              this.setState(state => {
                state.saveButton.inputNameBoot = valueInput;
                return {
                  saveButton: state.saveButton
                };
              });
            }}
            label="Boot name"
          />
          <input
            style={{marginRight: 20}}
            onChange={e => {
              var valueInput = e.target.value
              this.setState(state => {
                state.saveButton.inputDescription = valueInput;
                return {
                  saveButton: state.saveButton
                };
              });
            }}
           placeholder="Boot description"
            label="Description"
          />

          <MDBBtn
            style={{marginRight: 20}}
            onClick={() => {
              console.log("scenarioTestsGlobal saved", this.context.scenarioTestsGlobal)
              axios.post("/boot_save", {
                data: this.context.scenarioTestsGlobal,
                bootName: this.state.saveButton.inputNameBoot,
                description: this.state.saveButton.inputDescription
              }).then(response => {
                console.log(response)
              })
                .catch(err => {
                  console.log("post data error: ", err)
                })
              // console.log("saved boot");
            }}
            color="mdb-color"
          >
            Ok
          </MDBBtn>
          <MDBBtn
            onClick={() => {
              this.setState(state => {
                state.saveButton.openForm = false;
                return {
                  saveButton: state.saveButton
                };
              });
            }}
            color="mdb-color"
            outline
          >
            Cancel
          </MDBBtn>


          {/* <MDBCol>
            <MDBBtn
              onClick={() => {
                console.log("scenarioTestsGlobal saved", this.context.scenarioTestsGlobal)
                axios.post("/boot_save", {
                  data: this.context.scenarioTestsGlobal,
                  bootName: this.state.saveButton.inputNameBoot,
                  description: this.state.saveButton.inputDescription
                }).then(response => {
                  console.log(response)
                })
                  .catch(err => {
                    console.log("post data error: ", err)
                  })
                // console.log("saved boot");
              }}
              color="mdb-color"
            >
              Ok
            </MDBBtn>
            <MDBBtn
              onClick={() => {
                this.setState(state => {
                  state.saveButton.openForm = false;
                  return {
                    saveButton: state.saveButton
                  };
                });
              }}
              color="mdb-color"
              outline
            >
              Close
            </MDBBtn>
          </MDBCol> */}

        </MDBRow>
      );
    } else {
      return (
        <MDBBtn
          color="mdb-color"
          outline
          onClick={() => {
            this.setState(state => {
              state.saveButton.openForm = true;
              return {
                saveButton: state.saveButton
              };
            });
          }}
        >
          Save Boot
        </MDBBtn>
      );
    }
  };

  renderScenarioTests = scenarioTests => {
    if (scenarioTests === null) {
      return;
    }
    return scenarioTests.map((scenarioTest, index) => {
      return (
        <ScenarioTest
          name={scenarioTest.name}
          numLoop={scenarioTest.numLoop}
          key={scenarioTest.key}
          scenarioTestId={index}
        />
      );
    });
  };

  render() {
    return (
      <div>
        <ScenarioTestFactoryContext.Provider
          value={{
            scenarioTestSynchronousState: this.scenarioTestSynchronousState,
            actionSynchronousState: this.actionSynchronousState,
            deleteScenarioTest: this.deleteScenarioTest,
            scenarioTests: this.context.scenarioTestsGlobal,
            scenarioTestStoreName: this.scenarioTestStoreName,
            scenarioTestStoreNumloop: this.scenarioTestStoreNumloop,
            deleteAction: this.deleteAction,
            bootSocket: this.context.socketIoContext
          }}
        >
          {this.renderScenarioTests(this.context.scenarioTestsGlobal)}
          <MDBBtnGroup style={{ width: "100%", marginTop: 20 }}>
            <MDBBtn
              style={{ marginRight: 20 }}
              onClick={this.appendNewScenarioTest}
              color="mdb-color"
              outline
            // style={{width: "90%"}}
            >
              Create new test scenario
            </MDBBtn>
            {/* <MDBInput>NameBoot</MDBInput> */}
            {this.renderSaveButton()}
          </MDBBtnGroup>
        </ScenarioTestFactoryContext.Provider>
      </div>
    );
  }
}

export default ScenarioTestFactory;
