import React, { useDebugValue } from "react";

import "@fortawesome/fontawesome-free/css/all.min.css";
import "bootstrap-css-only/css/bootstrap.min.css";
import "mdbreact/dist/css/mdb.css";

import {
  MDBContainer,
  MDBBtn,
  MDBModal,
  MDBModalBody,
  MDBModalFooter,
  MDBInput,
  MDBCol,
  MDBRow,
  MDBProgress,
  MDBBtnGroup,
  MDBDropdown,
  MDBDropdownToggle,
  MDBDropdownMenu,
  MDBDropdownItem
} from "mdbreact";

import ToolTip from "./ToolTip"
import SwitchButton from "./SwitchButton"

import {ScenarioTestFactoryContext} from "../contextCreateBoot";

import uuidv4 from "../utility"
import Axios from "axios";

class Action extends React.Component {
  static contextType = ScenarioTestFactoryContext
  
  constructor(props) {
    super(props);

    this.state = {
      commandInputEmpty: false,
      logInputEmpty: false,
      commandInput: "",
      mouseEnterActionButton: false
    };
  }

  componentWillUpdate = ()=>{
  };

  componentWillMount = ()=>{
    this.store =  {
      showModal: false,

      pinSettings: [
        {
          pinName: "userButton",
          pinNum: 21,
          key: uuidv4(),
          actions: [],
          pinState: false,
          onMouseIn: false,
          minusButtonOnMouseDown: false,
          toolTip: null
        },
        {
          pinName: "antiCheatButton",
          key: uuidv4(),
          pinNum: 12,
          actions: [],
          pinState: false,
          onMouseIn: false,
          minusButtonOnMouseDown: false,
          toolTip: null
        },
        {
          pinName: "resetPin",
          key: uuidv4(),
          pinNum: 20,
          actions: [],
          pinState: false,
          onMouseIn: false,
          minusButtonOnMouseDown: false,
          toolTip: null
        },
        {
          pinName: "hallSensorPin",
          key: uuidv4(),
          pinNum: 16,
          actions: [],
          pinState: false,
          onMouseIn: false,
          minusButtonOnMouseDown: false,
          toolTip: null
        }
      ],
      commandSetting: {
        commands: [],
        commandInput: "",
        key: uuidv4()
      },

      expect: {
        timeoutError: 0,
        logDebugs: [],
        logDebugInput: '',
        invalidInputTimeoutError: ''
      },

      delayAction: {
        key: uuidv4(),
        delayTime: null,
        inputError: false
      },

      chooseFirmwareForBurning: {
        key: uuidv4(),
        firmwareVersions: [],
        firmwareChoosed: []
      }

    };
    this.store = {...this.store, ...this.context.scenarioTests[this.props.scenarioTestId].actions[this.props.actionId]}
    this.context.actionSynchronousState(this.props.scenarioTestId, this.props.actionId, this.store)
  }

  componentDidMount() {
    console.log("action componentDidMount")
    Axios.get('/api/getStoredFirmwareVersions').then(r=>{
      this.store.chooseFirmwareForBurning.firmwareVersions = r.data.firmwareVersions
      this.context.actionSynchronousState(this.props.scenarioTestId, this.props.actionId, this.store)
    })
  }

  renderChooseFirmwareForburningButton = () => {
    return (
      <MDBRow style={{ margin: "20px" }} key={this.store.chooseFirmwareForBurning.key}>
        <MDBCol>
        <MDBDropdown>
          <MDBDropdownToggle
            outline
            color="mdb-color"
          >Choose Firmware
          </MDBDropdownToggle>
          <MDBDropdownMenu>
          {(()=>{
               return this.store.chooseFirmwareForBurning.firmwareVersions.map(firmwareVersion=>{
                  return (
                    <MDBDropdownItem
                      key={uuidv4()}
                      onClick={()=>{
                        this.store.chooseFirmwareForBurning.firmwareChoosed = firmwareVersion
                        this.context.actionSynchronousState(this.props.scenarioTestId, this.props.actionId, this.store)
                      }}
                    > 
                      {firmwareVersion}
                    </MDBDropdownItem>
                  )
               })
          })()}
          </MDBDropdownMenu>
        </MDBDropdown>
        </MDBCol>
        <MDBCol>
          Firmware Choosed: {this.store.chooseFirmwareForBurning.firmwareChoosed}
        </MDBCol>   
      </MDBRow>
    )
  }

  toggleModal = () => {
    // this.setState({ showModal: !this.state.showModal });
    this.store.showModal = !this.store.showModal
    this.store.commandSetting.commandInput=""
    this.store.expect.logDebugInput=""
    this.context.actionSynchronousState(this.props.scenarioTestId, this.props.actionId, this.store)
  };

  closeModalHandle = () => {
    this.store.showModal = !this.store.showModal
    this.context.actionSynchronousState(this.props.scenarioTestId, this.props.actionId, this.store)
  };

  mousePositionGpioSettingButtonHandle = (mouseEnter, pinSetting) => {
    let indexPinSetting = this.store.pinSettings.indexOf(pinSetting);
    this.store.pinSettings[indexPinSetting].onMouseIn = mouseEnter;
    this.context.actionSynchronousState(this.props.scenarioTestId, this.props.actionId, this.store)
  }

  renderInputCommandGroup = commandSetting => {
    return(  
      <React.Fragment key={commandSetting.key}>
      <MDBRow style={{ margin: "20px" }}>
          <MDBCol>
            {/* render command into input command  */}
          <MDBInput label="Command" onChange={e=>{
            this.context.scenarioTests[this.props.scenarioTestId].actions[this.props.actionId].commandSetting.commandInput = e.target.value
            this.context.actionSynchronousState(this.props.scenarioTestId, this.props.actionId, this.store)
          }}
          />
          </MDBCol>
          <MDBCol>
          <MDBBtn outline color="mdb-color"
            // append new command 
            // the command has two args
            // 1. what command is 
            // 2. timelength command
            onClick={()=>{

              if (this.context.scenarioTests[this.props.scenarioTestId].actions[this.props.actionId].commandSetting.commandInput === "") {
                
                this.setState((state)=>{
                  return {
                    commandInputEmpty: true
                  }
                });

                return
              } else {
                this.setState((state)=>{
                  return {
                    commandInputEmpty: false
                  }
                });
              }

              this.store.commandSetting.commands.push({
                command:  this.store.commandSetting.commandInput,
                timeLength: 500,
                key: uuidv4()
                });
              
              this.context.actionSynchronousState(this.props.scenarioTestId, this.props.actionId, this.store);
              }}
              >
              <i className="fas fa-plus">Add</i> 
              </MDBBtn>
                {(()=>{if(this.state.commandInputEmpty){
                  return <dir style={{color: "red"}}> command input is empty </dir>
                }})()}
          </MDBCol>
        </MDBRow>
          
        <MDBRow style={{ margin: "20px" }}>
            {/* render command inputed  */}
          {(()=>{
            return this.context.scenarioTests[this.props.scenarioTestId].actions[this.props.actionId].commandSetting.commands.map(command=>{
              return (
              <MDBBtn 
              // remove choosed command 
              onClick={()=>{
                let id = this.context.scenarioTests[this.props.scenarioTestId].actions[this.props.actionId].commandSetting.commands.indexOf(command)
                this.context.scenarioTests[this.props.scenarioTestId].actions[this.props.actionId].commandSetting.commands.splice(id, 1);
                this.context.actionSynchronousState(this.props.scenarioTestId, this.props.actionId, this.store);
              }}
              key={command.key} 
              color="mdb-color" 
              >{command.command}</MDBBtn>)
            });
          })()}
        </MDBRow>
        </React.Fragment>)
  };

  renderDelayActions = () => {
    return (
      <MDBRow style={{ margin: "20px" }}>
        <MDBCol>
          <MDBInput
            key={this.store.delayAction.key}
            label={(()=>{
              if(this.context.scenarioTests[this.props.scenarioTestId].actions[this.props.actionId].delayAction.delayTime) {
                return "Delay: " + this.context.scenarioTests[this.props.scenarioTestId].actions[this.props.actionId].delayAction.delayTime + " ms" 
              } else {
                return "Delay Time"
              }
            })()}
            onChange={(e)=>{
              if (!isNaN(e.target.value)) {
                this.store.delayAction.delayTime = e.target.value
                this.store.delayAction.inputError = false
              } else {
                this.store.delayAction.inputError = true
              }

              this.context.actionSynchronousState(this.props.scenarioTestId, this.props.actionId, this.store);
            }}
          >
          </MDBInput>
          {(()=>{
            if (this.context.scenarioTests[this.props.scenarioTestId].actions[this.props.actionId].delayAction.inputError) {
              return (
                <p style={{color:"red"}}>You Must Input Number</p>
              )
            }
          })()}
        </MDBCol>
      </MDBRow>
    )
  }

  renderSettedGpioActions = actions => {
    if (actions === undefined) {
      return;
    }

    return actions.map(action => {
      if (action.on) {
        return (
          <ToolTip
            key={action.key}
            style={{
              backgroundColor: "#92a8d1",
              width: action.timeLength
            }}
            toolTipMs={action.timeLength}
          />
        );
      } else {
        return (
          <ToolTip
            key={action.key}
            style={{
              backgroundColor: "#7CFC00",
              width: action.timeLength
            }}
            toolTipMs={action.timeLength}
          />
        );
      }
    });
  };

  renderGpioButtonSettingGroup = pinSetting => {
    if (pinSetting.onMouseIn) {
      return (
        <MDBRow style={{ margin: "20px" }}> 
        <MDBBtnGroup>
          {/* Choose state gpio */}
          <SwitchButton
            pinState={pinSetting.pinState}
            onClick={() => {
              let indexPin = this.store.pinSettings.indexOf(pinSetting);
              this.store.pinSettings[indexPin].pinState = !this.store.pinSettings[indexPin].pinState;

              this.context.actionSynchronousState(this.props.scenarioTestId, this.props.actionId, this.store);
            }}
          />
          <MDBBtn
            // increase timeLength Action
            onClick={() => {
              let indexPin = this.store.pinSettings.indexOf(pinSetting);
              let lastIndexAction = this.store.pinSettings[indexPin].actions.length - 1;
              
              if (lastIndexAction === -1) {
                lastIndexAction = 0;
                this.store.pinSettings[indexPin].actions.push({
                  on: pinSetting.pinState,
                  timeLength: 0,
                  key: uuidv4()
                });
              }

              this.store.pinSettings[indexPin].actions[lastIndexAction].timeLength -= 50;

              if (this.store.pinSettings[indexPin].actions[lastIndexAction].timeLength < 0) {
                this.store.pinSettings[indexPin].actions.splice(-1, 1);
              }

              this.context.actionSynchronousState(this.props.scenarioTestId, this.props.actionId, this.store);
            }}
            outline
            color="mdb-color"
          >
            <i className="fas fa-minus" />
          </MDBBtn>
          <MDBBtn
          // Decrease timeLength Action
            onClick={() => {
              let indexPin = this.store.pinSettings.indexOf(pinSetting);
              let lastIndexAction = this.store.pinSettings[indexPin].actions.length - 1;
              
              if (lastIndexAction === -1) {
                lastIndexAction = 0;
                this.store.pinSettings[indexPin].actions.push({
                  on: pinSetting.pinState,
                  timeLength: 0,
                  key: uuidv4()
                });
              }

              if (this.store.pinSettings[indexPin].actions[lastIndexAction].on === pinSetting.pinState) {
                this.store.pinSettings[indexPin].actions[lastIndexAction].timeLength += 50;
              } else {
                this.store.pinSettings[indexPin].actions.push({
                  on: pinSetting.pinState,
                  timeLength: 50,
                  key: uuidv4()
                });
              }

              this.context.actionSynchronousState(this.props.scenarioTestId, this.props.actionId, this.store);
            }}

            outline
            
            color="mdb-color"
          >
            <i className="fas fa-plus" />
          </MDBBtn>{" "}
        </MDBBtnGroup>
        </MDBRow>
      );
    } else {
      return (
        <MDBBtn outline color="mdb-color">
          <i className="fas fa-bolt">{pinSetting.pinName}</i>
        </MDBBtn>
      );
    }
  };

  renderGpioActions = pinSettings => {
    return pinSettings.map(pinSetting => {
      return (
        <React.Fragment key={pinSetting.key}>
          {/* RENDER GPIO ACTIONS */}
          <MDBRow
            style={{
              margin: "20px"
            }}
            onMouseEnter={this.mousePositionGpioSettingButtonHandle.bind(this, true, pinSetting)}
            onMouseLeave={this.mousePositionGpioSettingButtonHandle.bind(this, false, pinSetting)}
          >
            {this.renderGpioButtonSettingGroup(pinSetting)}
          </MDBRow>
          <MDBRow style={{ margin: "20px" }}>
              {this.renderSettedGpioActions(pinSetting.actions)}
          </MDBRow>
          {/* RENDER GPIO ACTION */}
        </React.Fragment>
      );
    });
  };

  render() {
    return (
      <div>
        <MDBContainer>
          <div
            onMouseEnter={()=>{this.setState(state => {
              return {
                mouseEnterActionButton: true
              }
            })}}
            
            onMouseLeave={()=>{this.setState(state => {
              return {
                mouseEnterActionButton: false
              }
            })}}
          >
            <MDBBtn outline 
              color={(()=>{
                    if(this.props.type === "in testing..." || this.state.mouseEnterActionButton) {
                      return "warning"
                    } else  {
                      return "mdb-color"
                    }
                    })()} 
                
              onClick={this.toggleModal}
            >
              <i className="fas fa-cogs" /> {(()=>{
                if (this.state.mouseEnterActionButton) {
                  return <>Config Action</>
                } else {
                    return this.props.type
                }
              })()}
            </MDBBtn>
          </div>
          <MDBModal
            size="lg"
            isOpen={this.context.scenarioTests[this.props.scenarioTestId].actions[this.props.actionId].showModal}
            toggle={this.toggleModal}
          >
            <MDBModalBody>
              <MDBContainer style={{ margin: "60px" }}>
                {(()=>{
                  if (this.props.type === "gpio") {
                    return this.renderGpioActions(this.context.scenarioTests[this.props.scenarioTestId].actions[this.props.actionId].pinSettings)
                  
                  } 
                  
                  else if (this.props.type === "eraseFirmware") {
                    return 
                  } 
                  
                  else if (this.props.type ==="burnFirmware") {
                    return this.renderChooseFirmwareForburningButton()
                  }
                  
                  else if (this.props.type === "command") {
                   return this.renderInputCommandGroup(this.context.scenarioTests[this.props.scenarioTestId].actions[this.props.actionId].commandSetting)
                  } 

                  else if (this.props.type === "delay") {
                    return this.renderDelayActions()
                  }
                })()}

                <MDBRow style={{ margin: "20px" }}>
                  <MDBCol>
                    <MDBInput label="What do you expect from log debug ?"
                      onChange={(e)=>{
                        this.store.expect.logDebugInput = e.target.value
                        this.context.actionSynchronousState(this.props.scenarioTestId, this.props.actionId, this.store);
                      }}
                    />
                  </MDBCol>
                  <MDBCol>
                  <MDBBtn
                  // adding expect log debug  
                  outline 
                  color="mdb-color"
                    onClick={()=>{

                      if (this.store.expect.logDebugInput === "") {
                        
                        this.setState((state)=>{
                          return {
                            logInputEmpty: true
                          }
                        });

                        return
                      } else {
                        
                        this.setState((state)=>{
                          return {
                            logInputEmpty: false
                          }
                        });
                      
                      }

                      this.store.expect.logDebugs.push({
                        logDebug: this.store.expect.logDebugInput,
                        key: uuidv4()
                      });
                      this.context.actionSynchronousState(this.props.scenarioTestId, this.props.actionId, this.store);
                    }}
                  > Add </MDBBtn>

                  {(()=>{
                    if (this.state.logInputEmpty) {
                      return <div style={{color: "red"}}> you should input expect log output </div>
                    }
                  })()}

                  </MDBCol>        
                </MDBRow>
                <MDBRow style={{ margin: "20px" }}>
                  
                  {/* render expected output log */}
                    {(()=>{
                      return this.store.expect.logDebugs.map(logDebug=>{
                        return (
                        <MDBBtn
                          onClick={
                            ()=>{
                              let id = this.store.expect.logDebugs.indexOf(logDebug);
                              this.store.expect.logDebugs.splice(id, 1);
                              this.context.actionSynchronousState(this.props.scenarioTestId, this.props.actionId, this.store);
                          }
                        }  
                        color="mdb-color"
                        key={logDebug.key}
                        >{logDebug.logDebug}</MDBBtn>)
                      })
                    })()}
                  
                </MDBRow>

                {/* EXPECT OUTPUT LOG */}   
                <MDBRow style={{ margin: "20px" }}>
                  {/* get input timeout error */}
                  <MDBCol>  
                    <MDBInput label={"TimeOut Error: "+this.store.expect.timeoutError+' ms'} 
                    onChange={(e)=>{
                      this.store.expect.timeoutError = e.target.value;
                      
                      // valid on input 
                      if (isNaN(this.store.expect.timeoutError)) {
                        this.store.expect.invalidInputTimeoutError = "you must input number";
                      } else {
                        this.store.expect.invalidInputTimeoutError ='';
                      }
                      this.context.actionSynchronousState(this.props.scenarioTestId, this.props.actionId, this.store);
                  }}
                  />
                  {/* render ms invalid input */}
                  {(()=>{
                    if (this.store.expect.invalidInputTimeoutError) {
                      return <p style={{color:"red"}}>{this.store.expect.invalidInputTimeoutError}</p>
                    } else {
                      return 
                    }
                  })()}
                  </MDBCol>
                </MDBRow >

              </MDBContainer>
            </MDBModalBody>
            <MDBModalFooter>
            <MDBBtn outline color="warning" onClick={()=>{
                        this.context.deleteAction(this.props.scenarioTestId, this.props.actionId)
                      }}>
                            <i className="fas fa-cogs" /> Delete Action
              </MDBBtn>
              <MDBBtn outline color="mdb-color" onClick={this.toggleModal}>
                Close
              </MDBBtn>
            </MDBModalFooter>
          </MDBModal>
        </MDBContainer>
      </div>
    );
  }
}

export default Action