import React, { useDebugValue } from "react";

import "@fortawesome/fontawesome-free/css/all.min.css";
import "bootstrap-css-only/css/bootstrap.min.css";
import "mdbreact/dist/css/mdb.css";

import {
  MDBContainer,
  MDBBtn,
  MDBModal,
  MDBModalBody,
  MDBModalHeader,
  MDBModalFooter,
  MDBInput,
  MDBCol,
  MDBRow,
  MDBProgress,
  MDBBtnGroup,
  MDBTooltip,
  MDBDropdown,
  MDBDropdownToggle,
  MDBDropdownMenu,
  MDBDropdownItem,
} from "mdbreact";
import Action from "./Action"
import {ScenarioTestFactoryContext} from "../contextCreateBoot"
import uuidv4 from "../utility"


class ScenarioTest extends React.Component {
  static contextType = ScenarioTestFactoryContext
  constructor(props) {
    super(props);
    this.store = {}
    this.state = {
      inputNumLoopError: false,
      chooseActionTypes: true
    }
  }

  componentWillMount = ()=>{
    // console.log("componentWillMount-------> compose store with scenarioTest it belong to")
    this.store = {
      typeAction: {
        gpio: "gpio",
        eraseFirmware: "eraseFirmware",
        burnFirmware: "burnFirmware",
        command: "command",
        delay: "delay"
      },
      actions: [],
      name: "",
      numLoop: "",
      createActionButton: {
        mouseEnter: false,
        key: uuidv4()
      },
      scenarioTestId: this.props.scenarioTestId
    }

    this.store = {...this.store, ...this.context.scenarioTests[this.props.scenarioTestId]}

    // console.log(">>>>>>>>>componentWillMount")
    // console.log("this.store.scenarioTestId: ", this.store.scenarioTestId)
    // console.log("this.props.scenarioTestId: ", this.props.scenarioTestId)
  }

  componentWillUpdate = ()=>{
    // this.store = {...this.store ,...this.context.scenarioTests[this.props.scenarioTestId]}
  }

  createAction = (type) => {
    this.store.actions.push({
      type: type,
      name: null,
      key: uuidv4()
    })

    this.context.scenarioTestSynchronousState(this.props.scenarioTestId, this.store);
  };

  mouseEnterCreateActionButtonHandle = (mouseEnter) => {
    this.store.createActionButton.mouseEnter = mouseEnter
    this.context.scenarioTestSynchronousState(this.props.scenarioTestId, this.store);
  }

  renderActions = () => {
    // console.log("render for this.store.scenarioTestId: ", this.store.scenarioTestId)
    // console.log("render for this.props.scenarioTestId: ", this.props.scenarioTestId)
    return this.store.actions.map((action, id) => {
        return (<Action key={action.key} type={action.type} 
                  name={action.name} scenarioTestId={this.props.scenarioTestId}
                  actionId={id}
                />);
    });
  };

  renderCreateActionButton = () => {
    return (<div 
        onClick={()=>{
          this.setState(state=>{
            return {
              chooseActionTypes: !state.chooseActionTypes
            }
          })
        }}
      >

        <MDBDropdown>
          <MDBDropdownToggle
            color="mdb-color"
            outline
          >
            <i className="fas fa-plus"> Create Action </i>
          </MDBDropdownToggle>
          <MDBDropdownMenu>
              {(()=>{
                return Object.keys(this.store.typeAction).map((key, index)=>{
                  return (<MDBDropdownItem color="mdb-color" 
                            onClick={this.createAction.bind(this, this.store.typeAction[key])} 
                            type={this.store.typeAction[key]} 
                            key={uuidv4()}>
                                {key} 
                          </MDBDropdownItem> )
                  })
              })()}
          </MDBDropdownMenu>
        </MDBDropdown>
    </div>)
  }

  render() {
    return (
      <React.Fragment>
        <MDBRow>
          <MDBCol>
            <MDBInput label={(()=>{
                if (this.store.name === "") {
                  return "Input test scenario name"
                } else {
                  return "Setted Name: " + this.store.name
                }
              })()} 

              onChange={(e)=>{
                this.context.scenarioTestStoreName(this.props.scenarioTestId, e.target.value);
              }}
            />
          </MDBCol>
          <MDBCol>
            <MDBInput label={(()=>{
                if (this.store.numLoop === "") {
                  return "Loop number"
                } else {
                  return "Setted Num: " + this.store.numLoop
                }
              })()} 

              onChange={(e)=>{
                if (isNaN(e.target.value)) {
                  this.setState((state)=>{
                    return {
                      inputNumLoopError: true
                    }
                  });
                  return
                } else {
                  this.setState((state)=>{
                    return {
                      inputNumLoopError: false
                    }
                  })
                }

                this.context.scenarioTestStoreNumloop(this.props.scenarioTestId, e.target.value);
              }}/>

              {(()=>{if(this.state.inputNumLoopError){return <div style={{color: "red"}}>Num Loop Should Be A number</div>}})()}
          </MDBCol>
          <MDBCol>
            <div className="text-right">
            <MDBBtn outline color="warning" onClick={this.context.deleteScenarioTest.bind(null, this.props.scenarioTestId)}>
              <i className="fas fa-trash-alt"> Delete This Test </i>
            </MDBBtn>
            </div>
          </MDBCol>
        </MDBRow>
        <MDBRow>
          {this.renderActions()}
          {this.renderCreateActionButton()}
        </MDBRow>
      </React.Fragment>
    );
  }
}

export default ScenarioTest