    renderAutomaticTesting = () => {
        return (
            <MDBCard style={{marginTop: "20px"}}>
                <MDBCardBody>
                    <MDBCardTitle style={styleCardTittle}>PCB Automatic Test</MDBCardTitle>
                    <MDBListGroup>
                    {(()=>{
                        return this.state.automationTest.map(test=>{
                            if (test.inTesting) {
                                return (
                                    <MDBListGroupItem   
                                    style={{backgroundColor: "red"}}
                                    key={test.key}> 
                                        <h4 align="center"> In Testing... </h4>
                                    </MDBListGroupItem>
                                )
                            } else {
                                return (
                                    <MDBListGroupItem  
                                    color={(()=>{
                                        if(!test.result) {
                                            return "warning"
                                        } else {
                                            return "success"
                                        }
                                    })()} 
                                    style={{}}
                                    key={test.key}> 
                                    <MDBRow>
                                        <MDBCol>{test.name}</MDBCol>
                                        <MDBCol align="right" style={{width:"100%"}}>
                                            {(()=>{
                                                    if(test.result===true){
                                                        return <MDBBadge color="success">PASS</MDBBadge>
                                                    } else if (test.result===false) {
                                                        return <MDBBadge color="danger">FAIL</MDBBadge>
                                                    }
                                                })()}
                                        </MDBCol>
                                    </MDBRow> 
                                    </MDBListGroupItem>
                                )
                            }
                        })
                    })()}
                    </MDBListGroup>
                    <hr />
                    {(()=>{
                        for(let i of this.state.automationTest) {
                            let isTestingDone = true
                            this.state.automationTest.map(t=>{
                                if (t.result===null) {
                                    isTestingDone = false
                                }
                            })

                            if (!isTestingDone) {
                                return
                            }

                            if(i.result === false) {
                                return <MDBBadge color="danger" style={{width:"100%"}}> <h5> Kt Qu: Fail</h5> </MDBBadge>
                            }
                        }
                        return <MDBBadge color="success" style={{width:"100%"}}> <h5>Kt Qu: Pass</h5> </MDBBadge>
                    })()}
                </MDBCardBody>
            </MDBCard>        
            )
    }

