import React from "react"
import {AppContext}  from "../contextCreateBoot"
import { MDBTable, MDBTableBody, MDBTableHead } from 'mdbreact';
import { MDBBtn } from "mdbreact"
import uuidv4 from "../utility"


class TesingResult extends React.Component {
    static contextType = AppContext
    constructor(props) {
        super(props)
    }

    renderLogDebugsSearchedResult = (logDebugsSearchedResult) => {
        return (<div key={uuidv4()}>
            Pass:
                <ul>
                    {(()=>{
                        return logDebugsSearchedResult.pass.map(p=>{
                            return (<li>
                                {JSON.stringify(p)}
                            </li>)
                        })
                    })()}
                </ul>
            Fail:
                <ul>
                {(()=>{
                        return logDebugsSearchedResult.error.map(e=>{
                            return (<li>
                                {JSON.stringify(e)}
                            </li>)
                        })
                    })()}
                </ul>
        </div>)
    }

    renderTestingResultTable = (testingResult) => {
        return (
            <MDBTable key={uuidv4()}>
                <MDBTableHead>
                    <tr>
                        {(()=>{                            
                            return Object.keys(testingResult.results[0]).map(key=>{
                                return <th key={uuidv4()}>{key}</th>
                            })
                        })()}        
                    </tr>
                </MDBTableHead>
                    
                <MDBTableBody>
                        {(()=>{
                            return testingResult.results.map(result=>{
                                
                                return (
                                <tr>
                                    {(()=>{
                                        return Object.keys(result).map(key=>{
                                            if (key === "logDebugsSearchedResult") {
                                                console.log("render renderLogDebugsSearchedResult: ",result[key])
                                                return this.renderLogDebugsSearchedResult(result[key])
                                            } else {
                                                return (<td>{JSON.stringify(result[key])}</td>)
                                            }                                    
                                                        
                                        })
                                    })()}
                                </tr>)
                            })
                        })()}
                </MDBTableBody>
            </MDBTable>
        )
        
    }

    renderAllResult = () => {
        return this.context.testingResults.map((testingResult, id)=>{
            return (
                    <>
                        <MDBBtn
                            outline
                            color="mdb-color"
                            onClick={this.context.toogleShowDetailTestingResult.bind(null, id)}
                        >
                            Show Detail Testing For: {testingResult.testingName}
                        </MDBBtn>
                        <div>
                            {(()=>{
                                if (testingResult.showDetail) {
                                    return this.renderTestingResultTable(testingResult)
                                }
                            })()}
                        </div>
                    </>
                    )
            // return this.renderTestingResultTable(testingResult)
        })
    }

    render() {
        console.log("this.context.testingResults: ", this.context.testingResults)
        return (<>{this.renderAllResult()}</>)
    }
}

export default TesingResult;