import React from "react";

import PropTypes, { bool } from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Typography from "@material-ui/core/Typography";
import CreateBoot from "./CreateBoot";
import { AppContext, SocketIoContext } from "./contextCreateBoot";
import Qctool from "./Components/Qctool";
import SerialDisplay from "./SerialDisplay"

import { MDBCol, MDBRow, MDBBtn } from "mdbreact";
import ReactNotification from "react-notifications-component";
import "react-notifications-component/dist/theme.css";
import TesingResult from "./Components/TestingResult";
import ManualTest from "./Components/ManualTest"
// import 

function TabContainer(props) {
  return (
    <Typography component="div" style={{ padding: 8 * 3 }}>
      {props.children}
    </Typography>
  );
}

TabContainer.propTypes = {
  children: PropTypes.node.isRequired
};

const styles = theme => ({
  root: {
    flexGrow: 1,
    width: "100%",
    backgroundColor: theme.palette.background.paper
  }
});

class MainApp extends React.Component {
  static contextType = SocketIoContext;
  constructor(props) {
    super(props);
    this.state = {
      value: 0,
      editBoot: false,
      choosedBoot: null,
      scenarioTestsGlobal: [],
      openCreateBootView: false,
      bootSocket: null,
      hardwareInBusy: false,
      testingResults: [],
      connectToDeviceStatus: {
        type: null,
        port: null
      },
    };

    this.notificationDOMRef = React.createRef();
  }

  handleChange = (event, value) => {
    this.setState({ value });
  };

  addNotification = (title, message, type, duration) => {
    this.notificationDOMRef.current.addNotification({
      title: title,
      message: message,
      type: type,
      insert: "top",
      container: "top-right",
      animationIn: ["animated", "fadeIn"],
      animationOut: ["animated", "fadeOut"],
      dismiss: { duration: parseInt(duration) },
      dismissable: { click: true }
    });
  }

  setStoreScenarioTestsToGlobal = scenarioTests => {
    this.setState((state)=>{
      return {
        scenarioTestsGlobal: scenarioTests
      }
    })  
  };

  choosedBootHandle = (nameBoot, scenarioTests) => {
    // Mount the new ---> let's it update store
    console.log("choosed scenarioTests", scenarioTests)
    this.setState(state=>{
      return {
        choosedBoot: nameBoot,
        scenarioTestsGlobal: scenarioTests
      }
    });
  }

  pushNewTestingResult = (testingResultSend) => {
    this.setState(state=>{
      
      let elementNoExisted = true;
    
      state.testingResults.map((testingResult, id)=>{
        if (testingResult.testingName === testingResultSend.testingName){
          elementNoExisted = false
          console.log("testingResultSend", testingResultSend)
          return state.testingResults[id] = testingResultSend
        }
      });

      if (elementNoExisted) {
        testingResultSend.showDetail=false
        testingResultSend.showTestManual=false
        state.testingResults.push(testingResultSend)
      }

      console.log("testingResults: ", state.testingResults)

      return {
          testingResults: state.testingResults
      }
    })
  }

  setConnectToDeviceStatus = (status) => {
    this.setState(state=>{
      return {
        connectToDeviceStatus: status
      }
    })
  }

  setCreateBootView = (vale) => {
    this.setState(state=>{
      return {
        openCreateBootView: vale 
      }
    })
  }

  toogleShowDetailTestingResult = (testingResultIndex) => {
    this.setState(state=>{
      state.testingResults[testingResultIndex].showDetail ^= 1
      return {
        testingResults: state.testingResults
      }
    })
  }

  toogleShowTestManual = (testingResultIndex) => {
    this.setState(state=>{
      state.testingResults[testingResultIndex].showTestManual ^= 1
      return {
        testingResults: state.testingResults
      }
    })
  }

  setHardwareInBusy = (value) => {
    this.setState(state=>{
      return {
        hardwareInBusy: value
      }
    })
  }

  render() {
    const { classes } = this.props;
    const { value } = this.state;
    console.log("mainApp re-render")

    return (
      <div>
        <AppContext.Provider
          value={{
            scenarioTestsGlobal: this.state.scenarioTestsGlobal,
            setStoreScenarioTestsToGlobal: this.setStoreScenarioTestsToGlobal,
            choosedBootHandle: this.choosedBootHandle,
            setCreateBootView: this.setCreateBootView,
            socketIoContext: this.context.bootSocket,
            setHardwareInBusy: this.setHardwareInBusy,
            hardwareInBusy: this.state.hardwareInBusy,
            addNotification: this.addNotification,
            testingResults: this.state.testingResults,
            pushNewTestingResult: this.pushNewTestingResult,
            toogleShowDetailTestingResult: this.toogleShowDetailTestingResult,
            toogleShowTestManual: this.toogleShowTestManual,
            setConnectToDeviceStatus: this.setConnectToDeviceStatus,
            connectToDeviceStatus: this.state.connectToDeviceStatus
          }}
        >
          <AppBar position="static" style={{backgroundColor: "#f7bc42"}}>
            <Tabs
              value={value}
              onChange={this.handleChange}
              // indicatorColor="primary"
              textColor="white"
              variant="scrollable"
              scrollButtons="auto"
            >
              <Tab  label="Automation Test" />
              <Tab  label="Manual Test" />
              <Tab label="Testing Result" />
            </Tabs>
          </AppBar>
          <ReactNotification ref={this.notificationDOMRef} />
          {(() => {
            if (value === 0) {
              return (
                <TabContainer>
                  <MDBRow>
                    {/* <MDBCol sm="10"> */}
                      <Qctool />
                    {/* </MDBCol> */}
                  </MDBRow>
                  {/* Boot */}
                  <MDBRow>
                    <MDBCol sm="12">
                      {(() => {
                        if (this.state.editBoot || this.state.openCreateBootView) {
                          return  <CreateBoot />
                        } else {
                          if (this.state.choosedBoot) {
                            return (
                              <MDBBtn
                                color="mdb-color"
                                outline
                                onClick={() => {
                                  this.setState(state => {
                                    return {
                                      editBoot: true
                                    };
                                  });
                                }}
                                style={{ width: "100%" }}
                              >
                                Choosed Boot: {this.state.choosedBoot}.{" "}
                                <b>Would You Like To Edit Boot ?</b>
                              </MDBBtn>
                            );
                          } else {
                            return (
                              <MDBBtn
                                color="mdb-color"
                                outline
                                onClick={() => {
                                  this.setState(state => {
                                    return {
                                      editBoot: true
                                    };
                                  });
                                }}
                                style={{ width: "100%", marginTop: 20 }}
                              >
                                <b> Create Boot ? </b>
                              </MDBBtn>
                            );
                          }
                        }
                      })()}
                    </MDBCol>
                    <MDBCol>{/* debug */}</MDBCol>
                  </MDBRow>
                </TabContainer>
              );
            } else if (value === 1) {
              return (
                <>
                  <TabContainer> <ManualTest /></TabContainer>
                </>
              );
            } else if (value === 2) {
              return (<TabContainer> <TesingResult /></TabContainer>);
            }
          })()}
          {/* <SerialDisplay /> */}
        </AppContext.Provider>
      </div>
    );
  }
}

MainApp.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(MainApp);
