import React from 'react';
import {
    MDBBtn, MDBCard, MDBCardBody, MDBCardImage, MDBCardTitle,
    MDBCardText, MDBCol, MDBBtnGroup, MDBRow,
    MDBDropdownMenu, MDBDropdownItem, MDBDropdown,
    MDBDropdownToggle, MDBBadge
} from 'mdbreact';

// import {NotificationContainer, NotificationManager} from 'react-notifications';
import uuidv4 from "../utility"
import BootChoosing from "./BootChoosing";
import { AppContext } from "../contextCreateBoot"
import SerialDisplay from "../SerialDisplay"

class Qctool extends React.Component {
    static contextType = AppContext;

    constructor(props) {
        super(props)

        this.state = {
            boxInfos: {
                firmwareVersion: "",
                efuse: "",
                macAddress: "",
                imgsInFlash: "",
                brokenImageCount: "",
                batteryLevel: "",
                isWifiConnected: false,
                rssi: ""
            },

            statusDevice: {
                port: null,
                type: null
            },

            storedFirmwareVersions: [],

            statusBurningFirmware: "BurnFirmware",

            serialCommandInput: null,

            senddingSerialCommandFail: false
        }

        this.firmwareVersionStored = []
    }

    componentWillUnmount() {
        this.context.socketIoContext.off()
    }

    componentDidMount() {
        setTimeout(
            () => {
                this.context.socketIoContext.on("deviceBusy", dummy => {
                    this.context.setHardwareInBusy(true);
                });

                this.context.socketIoContext.on("bootBeginRunAction", (actionBeginRunningKey) => {
                    const scenarioTestsGlobal = [...this.context.scenarioTestsGlobal]
                    console.log("scenarioTestsGlobal: ", scenarioTestsGlobal)
                    scenarioTestsGlobal.map(ScenarioTest => {
                        ScenarioTest.actions.map((action, id) => {
                            if (action.key === actionBeginRunningKey) {
                                // console.log("bootBeginRunAction: ", actionBeginRunningKey)
                                ScenarioTest.actions[id].type = "in testing..."
                            }
                        })
                    })
                    this.context.setStoreScenarioTestsToGlobal(scenarioTestsGlobal)

                });

                this.context.socketIoContext.on("bootDoneRunAction", (actionDoneRunning) => {
                    // console.log("actionDone Running: ", actionDoneRunning)

                    const scenarioTestsGlobal = [...this.context.scenarioTestsGlobal]
                    scenarioTestsGlobal.map(ScenarioTest => {
                        ScenarioTest.actions.map(action => {
                            if (action.key === actionDoneRunning.key) {
                                action.type = actionDoneRunning.type
                            }
                        })
                    })
                    this.context.setStoreScenarioTestsToGlobal(scenarioTestsGlobal)
                });

                this.context.socketIoContext.on("connectToDeviceStatus", statusDevice => {
                    // console.log("socket set status device: ", statusDevice)
                    this.context.setConnectToDeviceStatus(statusDevice)
                });

                this.context.socketIoContext.on("storedFirmwareVersionsForQctool", (storedFirmwareVersions) => {
                    console.log("firmware: ", storedFirmwareVersions)
                    // this.firmwareVersionStored = storedFirmwareVersions
                    this.setState(state => {
                        return {
                            storedFirmwareVersions
                        }
                    })
                });

                this.context.socketIoContext.on("sendKeysStatus", status => {
                    this.context.setHardwareInBusy(false);
                    if (status == 'SendKeysSuccessFull') {
                        this.context.addNotification("sendKeysStatus", status, "success", 5000);
                    } else {
                        this.context.addNotification("sendKeysStatus", status, "danger", 5000);
                    }
                })

                this.context.socketIoContext.on("statusBurnFirmware", status => {
                    this.context.setHardwareInBusy(true);

                    if (status === "beginBurnFirmware") {
                        this.context.addNotification("statusBurnFirmware", status, "success", 3000);
                        this.setState(state => {
                            console.log("status: ", status)
                            return {
                                statusBurningFirmware: "Burning Firmware..."
                            }
                        });

                    } else if (status === "burnFirmwareSuccessFull") {
                        this.context.addNotification("statusBurnFirmware", status, "success", 3000);
                        this.setState(state => {
                            return {
                                statusBurningFirmware: "Burn Firmware"
                            }
                        });

                        this.context.setHardwareInBusy(false);

                    } else if (status === "burnFirmwareFail") {
                        this.context.addNotification("statusBurnFirmware", status, "danger", 5000);
                        this.setState(state => {
                            return {
                                statusBurningFirmware: "Burn Firmware"
                            }
                        });
                        this.context.setHardwareInBusy(false);
                    }
                });

                this.context.socketIoContext.on("foundExpectedLogdebug", (detailResult) => {
                    this.context.addNotification("foundExpectedLogdebug", JSON.stringify(detailResult), "success", 3000)
                });

                this.context.socketIoContext.on("showTestinResult", testingResult => {
                    this.context.pushNewTestingResult(testingResult)
                    console.log("pushed testing result: ", testingResult)
                })

                this.context.socketIoContext.on("eraseFirmwareStatus", status => {
                    if (status === "beginEraseFirmware") {
                        this.context.setHardwareInBusy(true);
                        this.context.addNotification("eraseFirmwareStatus", status, "success", 3000);
                    } else if (status === "eraseFirmwareDone") {
                        this.context.setHardwareInBusy(false);
                        this.context.addNotification("eraseFirmwareStatus", status, "success", 3000);
                    } else {
                        this.context.setHardwareInBusy(false);
                        this.context.addNotification("eraseFirmwareStatus", status, "danger", 3000);
                    }
                });

                this.context.socketIoContext.on("bootDoneRunTestAutomation", data => {
                    console.log("bootDoneRunTestAutomation")
                    this.context.setHardwareInBusy(false);
                });

                this.context.socketIoContext.on("serialSendingStatus", data => {
                    if (data === "success") {
                        this.setState(state => {
                            return {
                                senddingSerialCommandFail: false
                            }
                        });
                    } else {
                        this.setState(state => {
                            console.log("sending fail")
                            return {
                                senddingSerialCommandFail: true
                            }
                        });
                    }
                })

                // ask what is going on at hardware side
                this.context.socketIoContext.emit("qctoolRequestGetlistFirmware", "")
                this.context.socketIoContext.emit("requestStatusDevice", "")
            }, 1000);

        // turn hardware process on and regis for new socket event first
        this.context.socketIoContext.emit("turnQctoolToDiagnosisMode", "")
    }

    runAction = (action) => {
        this.context.socketIoContext.emit("runAction", action);
    }

    runAutomationTest = async () => {
        // scenarioTests = this.context.scenarioTestsGlobal}
        this.context.setHardwareInBusy(true)
        this.context.socketIoContext.emit("runBoot", {
            scenarioTests: this.context.scenarioTestsGlobal,
            testingName: uuidv4()
        })
    }

    renderInforQc = () => {
        return (
            <div style={{
                display: 'fex',
                flexDirection: 'column'
            }}>
                <h4>Box Info</h4>
                <ul key={uuidv4()}>
                    {Object.keys(this.state.boxInfos).map(key => {
                        return (<li key={uuidv4()}><h5>{key}: {this.state.boxInfos.key}</h5></li>)
                    })}
                </ul>
            </div>

        );
    }

    renderBootChoosingBt = () => (
        <BootChoosing disabled={this.context.hardwareInBusy} />
    )

    renderRunautomaticTestBt = () => (
        <MDBBtn
            outline
            disabled={this.context.hardwareInBusy}
            onClick={this.runAutomationTest}
            color="mdb-color">Run Automation Test
        </MDBBtn>
    )

    renderSerialCommandInput = () => (
        <div className="input-group">
            <input onChange={(e) => {
                var value = e.target.value
                this.setState(state => {
                    return {
                        serialCommandInput: value
                    }
                })
            }}
                onKeyDown={(e) => {
                    if (e.key === "Enter") {
                        this.context.socketIoContext.emit("sendSerial", this.state.serialCommandInput)
                    }
                }}
                style={(() => {
                    if (this.state.senddingSerialCommandFail) {
                        return {
                            color: "red"
                        }
                    } else {
                        return {
                            color: ""
                        }
                    }

                })()}
                // ref={this.serialCommandInputComponenent} 
                type="text" placeholder="Box Command"
                className="form-control form-control-lg" />
        </div>
    )

    renderBurnFirmwareButton = () => {
        return (
            <MDBDropdown>
                <MDBDropdownToggle
                    color="mdb-color"
                    outline
                    disabled={this.context.hardwareInBusy}
                >
                    {this.state.statusBurningFirmware}
                </MDBDropdownToggle>
                <MDBDropdownMenu
                >
                    {(() => {
                        return this.state.storedFirmwareVersions.map(firmwareVersion => {
                            return (
                                <MDBDropdownItem
                                    key={uuidv4()}
                                    onClick={() => {
                                        this.context.socketIoContext.emit("clientRequestBurnFirmware", firmwareVersion)
                                    }}
                                    color="mdb-color"
                                >
                                    {firmwareVersion}
                                </MDBDropdownItem>
                            )
                        })
                    })()}
                </MDBDropdownMenu>
            </MDBDropdown>
        )
    }

    renderEraseFirmwarePartiionButton = () => {
        return <MDBBtn
            color="mdb-color"
            outline
            disabled={this.context.hardwareInBusy}
            onClick={() => {
                this.context.setHardwareInBusy(true)
                this.context.socketIoContext.emit("eraseFirmware", "firmwarePartition")
            }}
        >
            ErasePartition
        </MDBBtn>
    }

    renderEraseFirmwareButton = () => {
        return (
            <>
                <MDBBtn
                    color="mdb-color"
                    outline
                    disabled={this.context.hardwareInBusy}
                    onClick={() => {
                        this.context.setHardwareInBusy(true)
                        this.context.socketIoContext.emit("eraseFirmware", "")
                    }}
                >
                    Erase All
                </MDBBtn>
            </>
        )
    }

    sendkeyBt = () => (
        <MDBBtn
            color="mdb-color"
            outline
            disabled={this.context.hardwareInBusy}
            onClick={() => {
                this.context.setHardwareInBusy(true);
                this.context.socketIoContext.emit('sendKeys', '')
            }}
        >
            Send Keys
        </MDBBtn>
    )

    renderStatusSerialPort = () => {
        return (
            <MDBBadge
                color={this.context.connectToDeviceStatus.type === "successful" ? "mdb-color" : "warning"}
                outline
            >
                Serial Port Connecting: {this.context.connectToDeviceStatus.port}
            </MDBBadge>)
    }

    renderPrintLabelBt = (
        <><MDBBtn color="mdb-color" outline>Print Label</MDBBtn></>
    )

    render() {
        return (
            <MDBCard style={{ width: "100%" }}>
                <MDBCardBody>
                    <MDBCardTitle>
                        {this.renderStatusSerialPort()}
                    </MDBCardTitle>
                    <MDBRow>
                        <MDBCol md="2">
                            {this.renderInforQc()}
                            <MDBRow>
                                <MDBCol>
                                    {this.renderBurnFirmwareButton()}
                                </MDBCol>
                                <MDBCol>
                                    {this.renderEraseFirmwareButton()}
                                </MDBCol>

                                <MDBCol>
                                    {this.renderEraseFirmwarePartiionButton()}
                                </MDBCol>
                                <MDBCol>
                                    {this.renderPrintLabelBt}
                                </MDBCol>
                                <MDBCol>
                                    {this.sendkeyBt()}
                                </MDBCol>
                                <MDBCol>
                                    {this.renderBootChoosingBt()}
                                </MDBCol>
                                <MDBCol>
                                    {this.renderRunautomaticTestBt()}
                                </MDBCol>
                            </MDBRow>
                            {/* <div style={{
                        display: 'flex',
                        flexWrap: 'wrap'
                    }}> */}

                            {/* 
                        {this.renderBurnFirmwareButton()}
                        {this.renderEraseFirmwareButton()}
                        {this.renderPrintLabelBt}
                        {this.sendkeyBt()}
                        {this.renderBootChoosingBt()}
                        {this.renderRunautomaticTestBt()} 
                        */}
                            {/* </div> */}
                            {/* {this.renderSerialCommandInput()} */}
                        </MDBCol>
                        <MDBCol fluid size={6}>
                            <SerialDisplay />
                        </MDBCol>
                    </MDBRow>
                </MDBCardBody>
            </MDBCard>)
    }
}



export default Qctool;